.abonements-tabs {
  position: relative;
  background-color: $color-ghost-white;
  padding: 120px 0;

  @include vp-1199 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 64px 0 71px;
  }
}

.abonements-tabs__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.abonements-tabs__content {
  margin-top: 38px;

  @include vp-1199 {
    margin-top: 45px;
  }

  @include vp-767 {
    margin-top: 35px;
  }
}

.abonements-tabs h2 {
  font-family: $ff-alumni;
  font-weight: $fw-semi-bold;
  font-size: $lh-large;
  line-height: $fz-xxlarge;
  letter-spacing: $ls-medium;
  color: $color-sapphire;
  margin: 0;
  margin-bottom: 17px;

  @include vp-1199 {
    font-size: $fz-xxbig;
    line-height: $lh-slarge;
  }

  @include vp-767 {
    font-size: $fz-big;
    line-height: $lh-xlbig;
    margin-bottom: 19px;
  }
}

.abonements-tabs__tabs ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  column-gap: 46px;
  padding: 0;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    min-width: 341px;
    bottom: 0px;
    height: 2px;
    background-color: rgba($color-grey, 0.2);
  }

  @include vp-1199 {
    column-gap: 52px;

    &::after {
      min-width: 332px;
    }
  }

  @include vp-767 {
    column-gap: 40px;

    &::after {
      min-width: 290px;
    }
  }
}

.abonements-tabs__button {
  display: block;
  font-family: $ff-roboto;
  font-weight: $fw-normal;
  font-size: $fz-medium;
  line-height: $lh-sbig;
  color: rgba($color-sapphire, 0.5);
  border: 2px solid transparent;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  padding-bottom: 12px;
  text-align: left;

  &:hover,
  &:focus {
    color: rgba($color-sapphire, 1);
    border-bottom: 2px solid $color-crimson;
  }

  &:active {
    border-bottom: 2px solid $color-crimson;
  }

  @include vp-1199 {
    font-size: $fz-xxxsmall;
    line-height: $lh-xxlxmedium;
  }

  @include vp-767 {
    font-size: $fz-xxsmall;
    line-height: $lh-xxmedium;
  }
}

.abonements-tabs__decoration {
  position: absolute;
  right: 0;
  bottom: -150px;

  @include vp-1199 {
    bottom: -125px;
  }

  @include vp-767 {
    display: none;
  }
}

.abonements-tabs__decoration img {
  width: 287px;
  height: 494px;
  display: block;

  @include vp-1199 {
    width: 347px;
    height: 444px;
  }
}
