@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Alumni Sans";

  font-display: swap;

  src:
  local("Alumni Sans"),
  local("Alumni Sans Regular"),
  url("../../fonts/AlumniSans-Regular.woff2") format("woff2"),
  url("./../fonts/AlumniSans-Regular.woff") format("woff");
}

